<template>
  <v-container>
    <v-card class="pa-1" elevation="12">
      <v-card-title>
        <span v-if="combat"> </span>
        <span v-else @click="hideInitiative"> </span>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="round++" id="roundBtn">{{
          round
        }}</v-btn>
        <button class="scroll-to-top-btn" @click="scrollToTop">
          <v-icon>mdi-arrow-up-bold</v-icon>
        </button>
        <v-spacer></v-spacer>

        <v-btn
          class="ml-3"
          color="primary"
          @click="Onclick_battleEndBtn"
          v-if="combat"
          ></v-btn
        >
        <v-btn
          class="ml-3"
          color="primary"
          @click="Onclick_battleStartBtn"
          v-else
          > </v-btn
        >
        <!-- <v-btn text @click="test"> </v-btn> -->
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="lists"
        :items-per-page="100"
        :sort-desc="sortDesc"
        :sort-by="sortBy"
        @click:sort="onSort"
        hide-default-footer
      >
        <template v-slot:item="{ item }">
          <tr :class="getRowClass(item)">
            <td>
              <v-checkbox
                :v-model="item.statusEffect"
                @change="onStatusEffectChange(item)"
              ></v-checkbox>
            </td>
            <td
              @mousedown="startLongPress($event, item, 'name')"
              @mouseup="cancelLongPress"
            >
              {{ item.name }}
            </td>
            <td
              @mousedown="startLongPress($event, item, 'ac')"
              @mouseup="cancelLongPress"
            >
              {{ item.ac }}
            </td>
            <td
              @mousedown="startLongPress($event, item, 'hp')"
              @mouseup="cancelLongPress"
            >
              {{ item.hp }}
            </td>
            <td>{{ item.nowHp }}</td>
            <td>{{ item.cumulativeDamage }}</td>
            <td>
              <v-text-field
                v-model="item.damageInput"
                @keyup.enter="onDamageInputEnter(item)"
                hide-details="auto"
                class="custom-text-field"
              ></v-text-field>
            </td>
            <td>{{ item.initiative }} {{ item.initiative_cal }}</td>
          </tr>
          <!-- <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr> -->
        </template>

        <template v-slot:footer>
          <!--    -->
          <v-row align="center" class="ma-0">
            <v-checkbox
              v-model="isMonsterInfoHidden"
              class="ml-7"
              id="monsterInfoHide"
            ></v-checkbox>
            <v-col cols="3">
              <v-autocomplete
                v-model="selectedMonster"
                :items="getMonsters"
                label=" "
                item-text="name"
                :search="search"
              ></v-autocomplete>
            </v-col>
            <v-col cols="1" class="mb-5">
              <v-text-field
                hide-details="auto"
                class="custom-text-field"
                v-model="monsterCnt"
              ></v-text-field>
            </v-col>
            <v-col>
              <v-btn color="primary" @click="addMonsterToList"
                > </v-btn
              >
            </v-col>
          </v-row>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
//import statusEffectCmp from "./statusEffectCmp.vue";

export default {
  name: "BattleCmp",
  components: {
    // statusEffectCmp,
  },
  data() {
    return {
      lists: [],
      headers: [
        { text: "", value: "statusEffect", sortable: false },
        {
          text: "",
          align: "start",
          value: "name",
          sortable: false,
        },
        { text: "AC", value: "ac", sortable: false },
        { text: "HP", value: "hp", sortable: false },
        { text: " HP", value: "nowHp", sortable: false },
        { text: "", value: "cumulativeDamage" },
        { text: " ", value: "damageInput", sortable: false },
        { text: "", value: "initiative", sortable: true },
      ],
      sortBy: "initiative", //   
      sortDesc: false, //    ()
      combat: false,
      selectedMonster: true,
      isMonsterInfoHidden: false,
      round: 0,
      editStatus: false,
      editStatusData: [],
      monsterCnt: 1,
      hideInitiativeFlag: false,
      longPressTimer: 0,
      search: null,
    };
  },
  computed: {
    ...mapGetters("monster", ["getMonsters"]),
    ...mapGetters("player", ["getPlayers"]),
  },
  created() {
    //this.lists = this.getPlayers;

    this.getPlayers.forEach((item) => {
      this.lists.push(item);
      item.statusEffect = false;
      item.nowHp = item.hp;
      item.cumulativeDamage = 0;
      console.log(item);
    });
  },
  methods: {
    /**
     *
     *   */
    Onclick_battleStartBtn() {
      this.combat = !this.combat;
      //       
      //   initiative 1 20   
      this.lists.forEach((item) => {
        const rand = Math.floor(Math.random() * 20) + 1;

        item.initiative_tmp = item.initiative;

        item.initiative = Number(item.initiative) + rand;
        item.initiative_cal = " (" + item.initiative_tmp + " + " + rand + ")";

        //item.nowHp = item.hp;
      });

      //       
      //    
      if (this.sortBy === "initiative") {
        //    'initiative' 
        this.sortBy = "name"; // 'name' 
      } else {
        //   
        this.sortBy = "initiative"; // 'initiative' 
      }
      this.sortDesc = !this.sortDesc; //   
    },
    /**
     *
     *   */
    onSort({ sortBy, sortDesc }) {
      //     
      this.sortBy = sortBy;
      this.sortDesc = sortDesc;
    },
    /**
     *  
     *   */
    onDamageInputEnter(idx) {
      const input = idx.damageInput.trim(); //   

      if (input.startsWith("+")) {
        //  +   
        const damage = parseInt(input.substring(1)) || 0; // +     

        idx.nowHp = Math.min(idx.hp, idx.nowHp + damage); //  hp
        idx.cumulativeDamage -= damage;
      } else if (input.startsWith("*")) {
        //  HP   hp   
        const damage = parseInt(input) || 0; //    0   

        idx.nowHp = Math.max(0, idx.nowHp - damage);
        idx.cumulativeDamage -= damage;
      } else {
        //    
        //        hp 
        const damage = parseInt(input) || 0; //    0   

        idx.nowHp = Math.max(0, idx.nowHp - damage);
        idx.cumulativeDamage += damage;
      }

      if (idx.nowHp == 0) {
        this.onStatusEffectChange(idx);
      }

      idx.damageInput = ""; //   
    },
    Onclick_battleEndBtn() {
      this.combat = !this.combat;

      //       .
      const updatedLists = this.lists.filter((item) => item.type !== "m");

      //     initiative .
      updatedLists.forEach((item) => {
        item.initiative = item.initiative_tmp;
        item.initiative_cal = "";
      });

      // lists .
      this.lists = updatedLists;
      this.round = 0;
    },
    /**
     *  
     */
    addMonsterToList() {
      if (this.selectedMonster) {
        const selectedMonster = this.getMonsters.find(
          (monster) => monster.name === this.selectedMonster
        );
        for (let i = 0; i < this.monsterCnt; i++) {
          if (selectedMonster) {
            const rand = Math.floor(Math.random() * 20 + 1);

            const initiative_cal = this.combat
              ? " (" + selectedMonster.initiative + " + " + rand + ")"
              : "";

            const initiative = this.combat
              ? selectedMonster.initiative + rand
              : selectedMonster.initiative;

            if (!this.isMonsterInfoHidden) {
              let nameTmp = "";

              if (1 < this.monsterCnt) {
                nameTmp = selectedMonster.name + "_" + (i + 1);
              } else {
                nameTmp = selectedMonster.name;
              }

              this.lists.push({
                name: nameTmp,
                ac: "???",
                ac_tmp: selectedMonster.ac,
                hp: "???",
                hp_tmp: selectedMonster.hp,
                nowHp: "???",
                nowHp_tmp: selectedMonster.hp,
                cumulativeDamage: 0,
                initiative: initiative,
                initiative_tmp: selectedMonster.initiative,
                initiative_cal: initiative_cal,
                damageInput: "",
                type: "m",
              });
            } else {
              this.lists.push({
                name: "???",
                name_tmp: selectedMonster.name,
                ac: "???",
                ac_tmp: selectedMonster.ac,
                hp: "???",
                hp_tmp: selectedMonster.hp,
                nowHp: "???",
                nowHp_tmp: selectedMonster.hp,
                cumulativeDamage: 0,
                initiative: initiative,
                initiative_tmp: selectedMonster.initiative,
                initiative_cal: initiative_cal,
                damageInput: "",
                type: "m",
              });
            }
          }
        }
      }

      //      .
      this.selectedMonster = null;

      this.monsterCnt = 1;
    },
    hideInitiative() {
      //this.hideInitiativeFlag = !this.hideInitiativeFlag;

      this.showInitiative = !this.showInitiative;
    },
    handleTableCellClick(item, field) {
      // ???     tmp  .

      if (item[field] === "???") {
        item[field] = item[`${field}_tmp`];
      }
    },
    startLongPress(event, item, field) {
      console.log("startLongPress " + this.longPressTimer);

      this.longPressTimer = setTimeout(() => {
        // 3     
        this.handleTableCellClick(item, field);
      }, 1000); // 3 (3000 )
    },

    cancelLongPress() {
      console.log("cancelLongPress " + this.longPressTimer);
      clearTimeout(this.longPressTimer);
      this.longPressTimer = null;
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth", //  
      });
    },
    move_init() {},
    getRowClass(item) {
      return {
        "red-background": item.type === "m", // 'm'  row    
        "green-background": item.type === "p", // 'p'  row    
        "gray-background": item.statusEffect, // statusEffect true    
      };
    },
    onStatusEffectChange(item) {
      // statusEffect    
      // item statusEffect     .
      // statusEffect true  ,   

      item.statusEffect = !item.statusEffect;

      item.statusEffect
        ? this.$set(item, "backgroundClass", "gray-background")
        : this.$delete(item, "backgroundClass");
    },
  },
  destroyed() {
    this.move_init();
  },
};
</script>

<style scoped>
.custom-text-field {
  width: 70px; /*    */
  border-bottom: none !important; /*   */
}

.scroll-to-top-btn {
  position: fixed;
  bottom: 20px; /*  :     */
  right: 20px; /*  :     */
  z-index: 1000;
  padding: 10px 20px;
  background-color: #007bff; /*   */
  color: white; /*    */
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  width: 40px; /*     . */
  height: 40px; /*     . */
  display: flex;
  justify-content: center; /*     */
  align-items: center; /*     */
}

.v-data-table {
  font-size: 20px; /*    . */
}

.v-data-table th,
.v-data-table td {
  font-size: 20px; /*     . */
}

.red-background {
  background-color: rgba(255, 0, 0, 0.502) !important;
}

.green-background {
  background-color: rgba(144, 238, 144, 0.464) !important;
}

.gray-background {
  background-color: rgba(0, 0, 0, 0.525) !important;
}
</style>
